import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack } from "@mui/material";
// utils
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import { getCurrencies } from "../../../redux/slices/generale";
import {
  addExchangeRate,
  updateExchangeRate,
  addExchangeRateSuccess,
  updateExchangeRateSuccess,
} from "../../../redux/slices/exchangerate";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

ExchangeRateNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentExchangeRate: PropTypes.object,
};

export default function ExchangeRateNewEditForm({
  isEdit,
  currentExchangeRate,
}) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NewExchangeRateSchema = Yup.object().shape({
    from_currency: Yup.string().required(
      t("field.required", {
        obj: t("field.from_currency"),
      })
    ),
    to_currency: Yup.string().required(
      t("field.required", {
        obj: t("field.to_currency"),
      })
    ),
    rate: Yup.number().required(
      t("field.required", {
        obj: t("field.rate"),
      })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      from_currency: currentExchangeRate?.from_currency || "",
      to_currency: currentExchangeRate?.to_currency || "",
      rate: currentExchangeRate?.rate || 0,
      keyId: currentExchangeRate?.id || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentExchangeRate]
  );

  const methods = useForm({
    resolver: yupResolver(NewExchangeRateSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (isEdit && currentExchangeRate) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentExchangeRate]);

  const onSubmit = async () => {
    try {
      if (isEdit) {
        const results = await updateExchangeRate(values);
        dispatch(updateExchangeRateSuccess(results));
      } else {
        const results = await addExchangeRate(values);
        dispatch(addExchangeRateSuccess(results));
      }
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.configuration.exchange_rate);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const { currencies } = useSelector((state) => state.generale);

  useEffect(() => {
    dispatch(getCurrencies());
  }, [dispatch]);

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFSelect
                name="from_currency"
                label={t("field.from_currency")}
                placeholder={t("field.from_currency")}
                disabled={isEdit}
              >
                <option value="" />
                {currencies?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>

              <RHFSelect
                name="to_currency"
                label={t("field.to_currency")}
                placeholder={t("field.to_currency")}
                disabled={isEdit}
              >
                <option value="" />
                {currencies?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="rate" label={t("field.rate")} />
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t("p_exchange_rate.exchange"),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
