import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import {
  getExchangeRate,
  getExchangeRates,
} from "../../redux/slices/exchangerate";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import ExchangeRateNewEditForm from "../../sections/@dashboard/configuration/ExchangeRateNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function ExchangeRateCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { exchangeRateKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentExchangeRate, setCurrentExchangeRate] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
  }, [exchangeRateKey]);

  const handleGetObject = async () => {
    try {
      const result = await getExchangeRate(Number(exchangeRateKey));
      setCurrentExchangeRate(result);
      dispatch(getExchangeRates());
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.configuration.exchange_rate);
    }
  };

  return (
    <Page
      title={`Configuration: ${
        !isEdit
          ? t("p_exchange_rate.new_exchange")
          : t("p_exchange_rate.edit_exchange")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={
            !isEdit
              ? t("p_exchange_rate.new_exchange")
              : t("p_exchange_rate.edit_exchange")
          }
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("params.configuration"),
              href: PATH_DASHBOARD.configuration.root,
            },
            {
              name: !isEdit
                ? t("p_exchange_rate.new_exchange")
                : `${t("p_exchange_rate.exchange")} ${
                    currentExchangeRate?.from_currency
                  } ${t("params.to")} ${currentExchangeRate?.to_currency}`,
            },
          ]}
        />

        <ExchangeRateNewEditForm
          isEdit={isEdit}
          currentExchangeRate={currentExchangeRate}
        />
      </Container>
    </Page>
  );
}
